.profile {
    padding: 20px;
    display:flex;
}
.profileImage {
    width: 100%;
    height: 20px;
}
.profilePhoto {
    margin-right: 10px;
}
.profilePhoto img {
    width: 250px;
    height: 250px;
}
.profileInfo div {
    margin-bottom: 10px;
}

.profileInfo span {
    font-weight: 700;
}
.contacts {
    margin-bottom: 10px;
}
.contactsTitle {
    margin-bottom: 10px;
}
.contactsItems {
    padding-left: 15px;
}
.contactsLink {
    color: #0000FFFF;
    cursor: pointer;
}

.contactsLink:hover {
    color: #0000c1;
}